"use client"

import { motion } from "framer-motion"
import Image from "next/image"

interface CertificateCardProps {
  title: string
  issuer: string
  date: string
  imageUrl: string
  onClick: () => void
}

export default function CertificateCard({ title, issuer, date, imageUrl, onClick }: CertificateCardProps) {
  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.95 }}
      whileInView={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.5 }}
      className="group relative aspect-[4/3] bg-white rounded-lg overflow-hidden shadow-md hover:shadow-xl transition-shadow duration-300 cursor-pointer"
      onClick={onClick}
    >
      <div className="absolute inset-0">
        <Image
          src={imageUrl}
          alt={title}
          fill
          className="object-cover"
        />
      </div>
      <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300">
        <div className="absolute bottom-0 left-0 right-0 p-4 text-white">
          <h3 className="text-lg font-medium mb-1 truncate">{title}</h3>
          <div className="flex items-center justify-between text-sm text-gray-300">
            <span>{issuer}</span>
            <span>{date}</span>
          </div>
        </div>
      </div>
    </motion.div>
  )
}
